import { faCommentsDollar } from "@fortawesome/free-solid-svg-icons"
import { useEffect , useState } from "react"
import './index.scss'


const data = require('../../Backend/Menu.json')

const Menu =()=>{
    const menu = getMenuItems(data)
    const [currentpage , setCurrentPage] = useState(1)
    const [itemsPerPage , setItemsPerPage] = useState(20)
    const lastIndex = currentpage * itemsPerPage
    const firstIndex = lastIndex - itemsPerPage
    const currentItems = menu.slice(firstIndex , lastIndex)
    return(
        <div className='menu-page'>
            <span style={{ fontSize: '1.5vw', backgroundColor: '#2d4341' ,padding:'1% 0 1% 0'}}>
                <h1>Menu Item</h1>
                <h1>Description</h1>
                <h1>Price</h1>
            </span>
            <div className="menu">
            {currentItems.map((item,index)=>{
                return(
                    <span key={index}>
                        <h1>{item.name}</h1>
                        <h2>{item.description}</h2>
                        <h1>{item.price}</h1>
                    </span>
                )
            })}
            </div>
            <Pagination  total ={menu.length} perPage={itemsPerPage} setCurrentPage={setCurrentPage} currentPage={currentpage}/>
        </div>
    )
}

function getMenuItems(data ){
    let keys = Object.keys(data)
    let allItems = [];

     for(let i = 0 ; i < keys.length; i++){
        const list = data[keys[i]].items[0].entries.items
        list.map((item)=>{
            item.entries.items.flatMap((item)=>{
            allItems.push(item)
            })
        })
    }
   
   return(allItems)
}

const Pagination =({total , perPage , setCurrentPage , currentPage})=>{
    let pages = []
    for (let int = 1; int <= Math.ceil(total/perPage); int++) {
        pages.push(int)
     
    }
    return(
        <div className='pagination'>
            {pages.map((page, index)=>{
                if(currentPage-1 === index){
                    return(<button key={index} onClick={()=>{setCurrentPage(page); window.scrollTo(0,0)}} className="active">{page}</button>) 
                }else{
                   return(<button key={index} onClick={()=>{setCurrentPage(page); window.scrollTo(0,0)}}>{page}</button>) 
                }
                
            })} 
        </div>
    )
}

export default Menu