import React from 'react'
import './index.scss'
import { CalendarComponent } from '@syncfusion/ej2-react-calendars'
import { useEffect } from 'react'

const Reservations = () => {
   const calender =  randomDates()
   useEffect(() => {
     
   }, [])
   
  return (
    <div
    className='reservation-page'
    style={{display:'flex' , marginTop:'4%' , justifyContent:'center'}}
    >
        <CalendarComponent 
          id="calendar" 
          values={calender}
        />
        <ReservationForm />
    </div>
  )
}


const ReservationForm =()=>{
  return(
    <form >
      <label>
        how many are in your group?
        <input type='range' min={1} max={10} />
        <p id='GS'>1</p>
      </label>
      <label>
        Name for Reservation
        <input type='text' placeholder='ex. D.Alexander' />
      </label>
      <label>
        phone number
        <input type='tel' />
      </label>
      <label>
        Date for Reservatioon
        <input type='date' />
      </label>
      <label style={{gridColumn:'span 2'}}>
        Special Requests(optional)
        <textarea placeholder='your request may be denied at restuarant for any reason' />
      </label>
      <button id='save' style={{gridColumn:'span 2'}}>Reserve</button>
    </form>
  )
}
function randomDates(){
    const start = new Date('2023-08-01')
    const end = new Date('2024-01-01')
    let current_reservations =[]
    for (let i = 0; i <= 100; i++) {
        const date = new Date(start.getTime() + Math.random() * (end.getTime() -start.getTime()))
        const summary = 'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Vestibulum ante augue, semper eu ante ac, fringilla vulputate elit. Donec nec vulputate massa, vitae pulvinar urna. Donec suscipit arcu nisi, et porta nisi congue id. Mauris lobortis nunc eget molestie rutrum. Nullam sit amet condimentum elit. Quisque in massa sed arcu molestie volutpat non vel neque. Sed non velit at nisl aliquam luctus. Nullam faucibus aliquam justo. Cras ac sodales mi. Donec tristique, nisl sed hendrerit lobortis, arcu ipsum placerat velit, non aliquet orci turpis eu ligula. Sed aliquam ex id turpis posuere facilisis. Etiam ut tellus ante. In turpis quam, bibendum fermentum tortor in, dictum eleifend leo.'
        current_reservations.push({date})
    }
    return current_reservations
}

export default Reservations